*, *::before, *::after{
    box-sizing: border-box;
  }
body {
    font-size: 16px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
    line-height: 1.5;
    a{
      text-decoration: none;
    }
}
h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
}


.container{
  max-width: 1110px;
  margin: 0 auto;
  @include pad{
    max-width: 690px;
  }
  @include mobile{
    max-width: 345px;
  }
}
